"use client";

import React, { useState, useEffect } from "react";
import { Settings, LogOut } from "lucide-react";
import { useRouter } from "next/navigation";
import Date from '../date/date'
import PersonalProfile from "./personalProfile/personalProfile";
import Notification from "./notification/notification";
import Security from "./security/Security";
interface HeaderProps {
  propValue: string;
}
export default function Header({propValue}: HeaderProps) {

  const [isDropdownOpen, setIsDropdownOpen] = useState(false);
  const [isPersonalProfile, setIsPersonalProfile] = useState(true);
  const [isNotification, setIsNotification] = useState(false);
  const [isSecurity, setIsSecurity] = useState(false);
  const router = useRouter();
  return (
    <header className="mx-4 my-2 rounded-lg bg-white shadow-sm relative">
      <div className="h-20 px-4 flex items-center justify-between">
        {/* Left side - Dashboard text */}
        <div className="relative">
          <a href="#" className="text-[#5D56BD] text-medium">
            {propValue}
          </a>
        </div>
        <Date/>
      </div>
      <div className="w-full px-4 py-2">
        <div className="flex space-x-6 text-sm text-[#5D56BD]">
          <div className= {`${isPersonalProfile ? 'bg-[#5D56BD] opacity-90' : ''}`}>Personal Profile</div>
            <div>Notification</div>
            <div>Security</div>
            {/* {<PersonalProfile/>}
            <Notification/>
            <Security/> */}
        </div>
      </div>
    </header>
  );
}